@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

@font-face {
    font-family:Monserrat ;
    src: url(/fonts/Montserrat-SemiBold.otf);
}

@font-face {
    font-family: Gothic;
    src: url(/fonts/Naville-Regular\ DEMO.ttf);
}


body{
    margin: 0;
    padding: 0;
}


.navbar img{
   width: 4vw;
}

.navbar{
    display: flex;
    align-items: center;
    width: 100vw;
    justify-content: space-evenly;
    margin-top: 3vh;
}

.navbar a{
    text-decoration: none;
    font-family: montserrat;
    color: black;
    font-weight: bold;
}

@media(max-width : 1000px){
    .navbar{
        display: none;
    }
}


.vision{
    background-image: url(/images/mens\ section.png);
    background-attachment: fixed;
    background-size: cover;
    background-position: center 0;
    background-repeat: no-repeat;
    width: 100vw;
    height: 50vh;
    text-align: center;
    margin-top: 5%;
}

.vision div{
    position: relative;
    top: 40%;
    color: white;
    filter: brightness(10);
}

.vision div h1{
    font-size: 2em;
    filter: brightness(1);
    font-family: without;
    text-shadow: black 1px 1px 1px;
}

.vision p{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-shadow: black 1px 1px 1px;
}

.text{
    margin-top: 5%;
    text-align: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.footer{
  display: flex;
  flex-direction: column;
  margin-top: 10%;
  background-color: whitesmoke;
}


.footer .first-part{
    display: flex;
    flex-direction: row;
    width: 100vw;
    justify-content: space-around;
}

.first-part .links{
    display: flex;
    flex-direction: column;
}

.links h1{
    font-family: Din;
    color: #013335;
}

.first-part .links a{
    text-decoration: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #013335;
}

.footer .second-part{
    position: relative;
    color: #013335;
}

.footer .follow{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: without;
    width: 100vw;
    text-align: center;
    justify-content: center;
}

.follow ion-icon{
    margin-left: 1vw;
}

.copyright{
    width: 100vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    margin-top: 6%;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.copyright span{
    text-decoration: underline;
    font-weight: bold;
}

.copyright img{
    width: 5vw;
}


@media (max-width : 1000px){
    .footer{
        display: flex;
        flex-direction: column;
    }

    .footer .first-part{
        display: flex;
        flex-direction: column;
        font-size: smaller;
        text-align: center;
    }

    .footer .second-part{
        position: relative;
        margin-top: 2%;
    }
    
}